.header {
  top: 0;
  left: 0;
  width: 100%;
  height: 4rem;
  background-color: #a0bbca;
  color: rgb(46, 30, 30);
  z-index: 10;
}

.customHeaderTitle {
	color: #f94040;
	font-Size:32px;
  padding: 3px;
	font-weight: normal;
	text-align: center;   
}
.iconborder {
  background-color: #d2e0e8;
  padding:3px;
  border-radius: 5px;
  align-items: center;
  position: absolute;
  display: flex;
  right: 10px;
  top: 10px;
}
.leftbutton{
  background-color: #d2e0e8;
  padding: 5px;
  border-radius: 5px;
  /* position: absolute; */
   top: 10px!important;
}
